body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing-container {
  height: 100vh;
  background: #000;
  background-image: url(/pokemon-list-app/static/media/pokeballs.6563d6fb.jpg);
}

.pokeball-wrapper {
  height: 300px;
}
.pokeball-wrapper .card-title {
  font-size: 2rem;
  margin-bottom: 0.5em;
}
.pokeball-wrapper .card-text {
  margin-top: auto;
  font-size: 1.5rem;
  font-weight: bold;
}

.pokeball-image {
  width: 150px;
  height: 150px;
  transition: 0.3s ease-in-out;
}
.pokeball-image:hover {
  -webkit-transform: scale(1.2) rotate(360deg);
          transform: scale(1.2) rotate(360deg);
}
.pokeball-image:hover.pokeball-image {
  margin-bottom: 1em;
}

nav {
  height: 100px;
  padding: 2em;
  margin-bottom: 2em;
  border-bottom: 1px solid #000;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
nav .nav-image {
  width: 50px;
  height: 50px;
  transition: 0.3s ease-in-out;
}
nav .nav-image:hover {
  -webkit-transform: scale(1.2) rotate(360deg);
          transform: scale(1.2) rotate(360deg);
}
nav .theme-icon:hover {
  cursor: pointer;
}

.main-nav {
  background-color: rgb(255, 144, 144);
}

/* SPINNER */
.spinner-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.spinner {
  width: 250px;
  height: 250px;
  position: absolute;
  top: calc(50% - 125px);
  left: calc(50% - 125px);
}

.card-link {
  text-decoration: none;
}
.card-link .card-pokemon:hover {
  box-shadow: 0 0 6px 1px rgba(170, 170, 170, 0.5);
}
.card-link .img {
  display: block;
  margin: 0 auto;
  width: 250px;
  height: 250px;
}
.card-link .card-title {
  text-transform: capitalize;
  font-size: 36px;
  text-align: center;
}

.grass {
  background-color: rgb(229, 255, 229);
}

.fire {
  background-color: rgb(255, 241, 236);
}

.water {
  background-color: rgb(228, 243, 255);
}

.electric {
  background-color: rgb(255, 252, 216);
}

.ground {
  background-color: rgb(255, 242, 215);
}

.psychic {
  background-color: rgb(236, 226, 253);
}

.normal {
  background-color: rgb(241, 241, 241);
}

.carousel img {
  width: 200px;
  height: 200px;
  margin: 0 auto;
}

.move-name {
  text-transform: capitalize;
  font-weight: 700;
}

.move-count {
  font-size: 20px;
  border-radius: 50%;
  padding: 0.2em;
  background-color: rgb(248, 187, 72);
  color: #fff;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.large {
  font-size: 32px;
}/*# sourceMappingURL=main.css.map */
